/**
 * URL处理封装
 * @param config
 */

const TDUrl = {
  parseURL(url) { // 把url的参数部分转化成json对象
    try {
      var a = document.createElement('a')
      a.href = url
      return {
        source: url,
        protocol: a.protocol.replace(':', ''),
        host: a.hostname,
        port: a.port,
        query: a.search,
        params: (function () {
          let ret = {}
          let seg = a.search.replace(/^\?/, '').split('&')
          let len = seg.length
          let i = 0
          let s
          for (; i < len; i++) {
            if (!seg[i]) {
              continue
            }
            s = seg[i].split('=')
            ret[s[0]] = s[1]
          }
          return ret
        })(),
        file: (a.pathname.match(/\/([^\/?#]+)$/i) || ['', ''])[1],
        hash: a.hash.replace('#', ''),
        path: a.pathname.replace(/^([^\/])/, '/$1'),
        relative: (a.href.match(/tps?:\/\/[^\/]+(.+)/) || ['', ''])[1],
        segments: a.pathname.replace(/^\//, '').split('/')
      }
    } catch (e) {
      this.printErrorLog(e)
      return {}
    }
  },
  parseURLParam(url) { // 把url的参数部分转化成json对象
    try {
      let targeturl = url || window.location.href
      const regUrl = /^\?([\w\W]+)$/
      const regPara = /([^&=]+)=([\w\W]*?)(&|$|#)/g
      let arrUrl = regUrl.exec(targeturl)
      let ret = {}
      if (arrUrl && arrUrl[1]) {
        let strPara = arrUrl[1]
        let result
        while ((result = regPara.exec(strPara)) != null) {
          ret[result[1]] = result[2]
        }
      }
      return ret
    } catch (e) {
      this.printErrorLog(e)
      return {}
    }
  },
  getURLParam(name, targetUrl) { // 通过key获取url中的参数值
    try {
      let reg = new RegExp('[?&]' + name + '=([^&]+)')
      targetUrl = targetUrl || window.location.search

      return targetUrl.match(reg) ? RegExp.$1 : null
    } catch (e) {
      this.printErrorLog(e)
      return ''
    }
  },
  printErrorLog(e) { // 打印参数日志
    console.log('error name:', e.name)
    console.log('error message:', e.message)
    console.log('error description:', e.description)
  }
}

export default TDUrl
